* {
  box-sizing: border-box;
}

.App {
  display: flex;
  text-align: center;
}

.App-header {
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5em;
  color: white;
  width: 70vw;
}

.most-recent-card {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
  overflow-y: scroll;
}

.card-draw-area {
  height: 10rem;
  width: 100%;
  border-top: 1px solid white;
}

.buttons {
  display: flex;
  justify-content: space-around; 
}

.buttons > button {
  padding: 1rem;
}

.card-history {
  max-height: 100vh;
  width: 30vw;
  padding: 1rem 1rem 1rem 0;
  overflow-y: scroll;
}

.previous-card:nth-child(odd) {
  background-color: #c5f6bb;
}

.previous-card:nth-child(even) {
  background-color: #bbf6f4;
}

@media screen and (max-width: 576px) {
  .App {
    flex-direction: column;
    height: 100vh;
  }
  .App-header {
    height: initial;
    width: 100%;
    flex-direction: column-reverse;
    font-size: 1em;
  }
  .card-draw-area {
    height: initial;
  }
  .most-recent-card {
    padding: 0.5rem;
  }
  .card-history {
    width: 100%;
  }
}
